@keyframes main-img {
	0% { box-shadow: unset; transform: scale(0.3); opacity: 0; }
	30% { box-shadow: unset; transform: scale(0.3); opacity: 1; }
	75% { box-shadow: unset; transform: scale(0.99); }
	100% { box-shadow: 0 0 1rem 0.9rem rgba(0, 0, 0, 0.1); transform: scale(1); }
}

@keyframes main-text {
	0% { opacity: 0; transform: translateY(-1rem); }
	62.5% { opacity: 0; transform: translateY(-1rem); }
	100% { opacity: 1; transform: translateY(0); }
}

/**********************************************************************************************************************/
:root {
	--main-img-animation: 5s
}

img {max-width: 90% !important;}

/**********************************************************************************************************************/
section {
	height: 100vh;
	overflow: hidden;
}

/**********************************************************************************************************************/
header {
	position: sticky;
	top: 0;
	left: 0;
	right: 0;
	min-height: 5rem;
	background: rgba(22, 49, 52, 0.23);
	display: flex;
	justify-content: space-around;
	align-items: center;
	box-shadow: 0 0 0.6rem 0.5rem rgba(0, 0, 0, 0.1);
	color: rgba(255, 255, 255, 0.9);
	-webkit-backdrop-filter: unset;
	backdrop-filter: blur(1rem);
	z-index: 9;
	margin: 0;
}

header > span {
	background: white;
	height: 3rem;
	width: 3rem;
	color: #d0d4d5;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 100%;
	box-shadow: 0 0 0.5rem 0rem;
	font-size: 5rem;
}


header a {
	background: white;
	padding: 0.5rem 1rem;
	border-radius: 5rem;
	color: black;
	box-shadow: 0 0  0.3rem 0.1rem rgba(0,0,0,0.1);
}

/**********************************************************************************************************************/
section#main,
section#main > div {
	background: #efeeec;
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	flex-direction: column;
}

section#main > div > img {
	width: 70vh !important;
	border-radius: 100%;
	box-shadow: 0 0 1rem 0.9rem rgba(0, 0, 0, 0.1); width: 70%;
	animation-name: main-img;
	animation-duration: var(--main-img-animation);
}

section#main > div > img + span {
	font-size: 1.5rem;
	margin-top: 1.5rem;
	opacity: 1;
	transform: translateY(0);
	animation-name: main-text;
	animation-duration: 8s;
}

section#main > div > img + span:after {
	content: "";
	display: block;
	width: 5rem;
	margin: 0 auto;
	border-bottom: 0.3rem solid #264b39;
	padding-bottom: 0.5rem;
}

/**********************************************************************************************************************/
section#facts {
	background: #010109 url("/images/f-logo.png") no-repeat bottom right;
	background-size: 45%;
	position: relative;
	height: 180vh;
}

section#facts h2 {
	color: white;
	font-size: 2.5rem;
	text-align: center;
}

section#facts:after {
	content: "";
	display: block;
	height: 70vh;
	perspective: 4000px;
}

section#facts:before {
	content: "";
	display: block;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: linear-gradient(45deg, #420101, #00000000, #420101);
}

section#facts li {
	font-size: 2rem;
	color: rgba(255, 255, 255, 0.8);
	list-style: none;
	margin-bottom: 2rem;
	line-height: 1;
}

/**********************************************************************************************************************/
section#platform-about {height: 150vh}

section#platform-about span {line-height: 1.2;}

section#platform-about span b {
	background: black;
	color: white;
}

section#platform-about > div {margin-top: 6.5rem;}

/**********************************************************************************************************************/
section#numbers {margin-top: -100vh}

section#numbers h2 {
	backdrop-filter: blur(0.3rem);
	padding: 5rem 0;
	background: rgba(255, 255, 255, 0.4);
}

/**********************************************************************************************************************/
section#application {
	position: relative;
	height: 230vh;
	background: #e8e8e8;

}

section#application img {
	margin: 0 auto;
	display: block;
	width: auto;
	position: absolute;
	left: 0;
	right: 0;
}

section#application div.motion {
	box-shadow: 0 -12rem 16rem 20px rgba(0, 0, 0, 0.5);
}

section#application div.motion > img {
	position: unset !important;
}

/**********************************************************************************************************************/
section#application-points {
	background-size: 80%;
	background: #e8e8e8 url("/images/app-bg.png") no-repeat;
	background-position-x: 500px;
	background-position-y: top;
	height: 500vh;
}

section#application-points li {
	list-style: none;
	font-size: 1.5rem;
	margin-bottom: 1.5rem;
}

section#application-points img#app {
	/*margin: -20vh auto 0;*/
	margin: 0 auto;
	display: block;
}

section#application-points > div.container{
	display: flex;
	align-items: center;
	justify-content: center;
	margin-top: -5rem;
}